.product-group-selector {
    margin-top: 2rem;
}

.product-group-selector, .page-links {
    text-align: center;
}

@media only screen and (min-width: 576px){
    .product-group-selector {
        text-align: left;
    }

    .page-links {
        text-align: right;
    }

    .pagination-section {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
}


